@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.ContentView {
  width: 100%;
  margin: 0 auto;
}
.slogan {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.slogan :nth-child(1) {
  text-align: center;
  font-family: "NotoSansHans-Bold", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 36px;
  color: #000000;
  letter-spacing: 1.12px;
  text-align: left;
  line-height: 50px;
  margin-top: 28px;
  white-space: nowrap;
}
.slogan :nth-child(2) {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 20px;
  color: #222222;
  text-align: left;
  line-height: 20px;
  margin-top: 10px;
}
.decorationImage {
  position: absolute;
  top: -39px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/leaf.png') bottom no-repeat;
  width: 39px;
  height: 39px;
  background-size: contain;
}
.belowPannel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin-bottom: 50px;
}
.belowPannel .cloumnDiv span:nth-child(2) {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  height: 16px;
  line-height: 16px;
  color: #212121;
  text-align: left;
  display: block;
  margin-bottom: 15px;
}
.belowPannel .cloumnDiv span:nth-child(3) {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #424242;
  line-height: 25px;
  text-align: center;
}
.belowPannel .cloumnDiv {
  display: block;
  width: 30%;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.belowPannel .iconSize {
  width: 28px;
  height: 32px;
  min-height: 32px;
  min-width: 28px;
  margin-bottom: 15px;
}
.belowPannel .icon1 {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/belowPannel/light.png") no-repeat center;
  width: 36px;
  height: 36px;
  min-height: 36px;
  min-width: 36px;
  background-size: contain;
}
.belowPannel .icon2 {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/belowPannel/security.png") no-repeat center;
  background-size: contain;
}
.belowPannel .icon3 {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/belowPannel/support.png") no-repeat center;
  background-size: contain;
}
.belowPannel .icon4 {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/belowPannel/quality.png") no-repeat center;
  background-size: contain;
}
.belowPannel .icon5 {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/belowPannel/change.png") no-repeat center;
  background-size: contain;
}
.belowPannel .icon6 {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/belowPannel/upload.png") no-repeat center;
  background-size: contain;
}
.aboutContent {
  margin-top: 19px;
}
.aboutContent .leftTxt {
  line-height: 20px;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 20px;
  color: #000000;
  text-align: left;
  border-bottom: 4px #EB6331 solid;
  padding-bottom: 16px;
  margin-right: 103px;
}
.aboutContent .ant-row {
  height: 100% !important;
}
.aboutContent .ant-col-8 {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 60%;
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #231815;
}
.aboutContent .ant-col-8:nth-child(1) div {
  margin-top: 41px;
  width: 94px;
  height: 94px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/insightful.png') no-repeat center;
  background-size: contain;
}
.aboutContent .ant-col-8:nth-child(2) div {
  margin-top: 41px;
  width: 94px;
  height: 94px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/efficient.png') no-repeat center;
  background-size: contain;
}
.aboutContent .ant-col-8:nth-child(3) div {
  margin-top: 41px;
  width: 94px;
  height: 94px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/expressive.png') no-repeat center;
  background-size: contain;
}
@media screen and (max-width: 1080px) {
  .factSheetAndReport {
    width: 100%;
  }
}
@media screen and (max-width: 880px) {
  .factSheetAndReport {
    width: 100%;
  }
  .factSheetAndReport .ant-col-12 {
    padding: 0px 20px;
  }
  .howtoConvert {
    padding-top: 35px;
    padding-bottom: 65px;
    flex-direction: column-reverse !important;
  }
  .howtoConvert div:nth-child(2) {
    margin-left: 0px !important;
    width: 80%;
  }
  .howtoConvert div:nth-child(2) span:nth-child(1) {
    margin-bottom: 34px;
  }
  .howtoConvert div:nth-child(2) span:nth-child(2) {
    margin-bottom: 15px;
  }
  .howtoConvert div:nth-child(1) {
    margin-left: -10%;
  }
}
.howtoConvert {
  padding-top: 60px;
  padding-bottom: 71px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.howtoConvert > div:nth-child(1) {
  width: 306px;
  height: 100px;
  background: url(/static/media/json2pdf.8f7cbcc3.png) no-repeat center;
  background-size: contain;
}
.howtoConvert > div:nth-child(2) {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-left: 98px;
}
.howtoConvert > div:nth-child(2) span:nth-child(1) {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  line-height: 16px;
  font-size: 16px;
  color: #212121;
  text-align: left;
  margin-bottom: 34px;
}
.howtoConvert > div:nth-child(2) span {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #0F0F0F;
  line-height: 24px;
  margin-bottom: 26px;
}
